<script>

export default {
  data(){
    return{
      search_dni: "",
    }
  },

  methods: {
    async searchDni(){

      this.$store.commit("incidente/set_dni_search", this.search_dni)

      await this.$store.dispatch("incidente/get_incident_list")
    },

    debounceGetIncident() {
      clearTimeout(this.debounceTimeout);
      this.debounceTimeout = setTimeout(() => {
        this.searchDni();
      }, 300);
    }

    },
}

</script>

<template>
  <div>
    <vs-input block primary maxlength="8"  type="search" aria-valuemax="8" state="primary"  v-model="search_dni" placeholder="Buscar DNI" @input="debounceGetIncident" />
  </div>
</template>

