<script>
export default {
  props: {
    item_incident: {
      type: Object,
      default: null,
    },
  },

  data() {
    return {
      isActive: true,
      isLoading:false,
      is_dni:true,
      id: null,
      dni_applicant: "",
      observation: "",
      activeButton:null,
      confirm_deletion: false

    };
  },

  computed:{},

  mounted() {
    this.activeButton = 0;
    if (this.item_incident) this.editableData();

  },

  methods: {
    editableData(){
      if (!this.item_incident) return;
      this.id = this.item_incident.id;
      this.dni_applicant = this.item_incident.dni_applicant;
      this.observation = this.item_incident.observation;
    },

    Close() {
      this.$store.commit("incidente/set_selected_incident", null)
      this.$emit("closeRegistration" , false);
    },

    validate_dni_applicant() {
      const dniRegex = /^[0-9]{8}$/; // Acepta solo 8 dígitos del 0 al 9
      return dniRegex.test(this.dni_applicant);
    },

    validate_data() {
      if(!this.dni_applicant && !this.observation ){
        this.$vs.notification({
          title: "Registro de Incidentes",
          text: "Ingresar  datos del incidente 👍",
          color: "danger",
        });
        return false;
      }

      if (!this.dni_applicant) {
        this.$vs.notification({
          title: "Registro de Incidentes",
          text: "Ingrese el DNI del postulante 👍",
          color: "danger",
        });
        return false;
      }

      this.is_dni = this.validate_dni_applicant();

      if(!this.validate_dni_applicant()){
        this.$vs.notification({
          title: "Registro de Incidentes",
          text: "Ingrese un DNI válido 👍",
          color: "danger",
        });
        return false;
      }

      if (!this.observation) {
        this.$vs.notification({
          title: "Registro de Incidentes",
          text: "Ingrese la observación del incidente 👍",
          color: "danger",
        });
        return false;
      }

      return true;
    },

    async save_incident() {

      if (!this.validate_data()) return;

      this.isLoading = true;
      const data = {
        dni_applicant: this.dni_applicant,
        observation: this.observation,
      };

      if (this.id) data.id = this.id;

      const response = this.id
          ? await this.$store.dispatch("incidente/update_incident",data)
          : await this.$store.dispatch("incidente/create_incident",data);

      this.$vs.notification({
        title: "Registro de Incidentes",
        text: response.error ? response.error : "Incidente registrado correctamente. 😊",
        color: response.error ? "danger" : "success",
      });

      if (!response.error) {
        await this.$store.dispatch("incidente/get_incident_list")
        this.Close();
      }

      this.isLoading = false;
    },

    async deleteIncident(){

      if(!this.id) return;
      this.isLoading = true;

      const status = await this.$store.dispatch("incidente/delete_incident",this.id);

      if (status) {
        this.$vs.notification({
          title: "Eliminación de Incidentes",
          text: "Incidente eliminado correctamente. 😊",
          color: "success",
        });
        await this.$store.dispatch("incidente/get_incident_list")
      }else {
        this.$vs.notification({
          title: "Eliminación de Incidentes",
          text: "Error al eliminar el incidente. 😢",
          color: "danger",
        });
      }
      this.confirm_deletion = false;
      this.Close();
      this.isLoading = false;
    },

    confirmDeletion(){
      this.confirm_deletion = true;
    },


  },
}
</script>

<template>
<div>
  <vs-dialog blur v-on:close="Close" prevent-close  v-model="isActive" title="Registro de Incidentes"  width="400px" >

    <template #header>
      <div v-if="id"  class="w-full mb-4 ">
        <vs-button-group  >
          <vs-button transparent  block :active="activeButton === 0" @click="activeButton = 0" >
            Editar Incidente
          </vs-button>
          <vs-button transparent  block  :active="activeButton === 1" @click="activeButton = 1" >
            Eliminar Incidente
          </vs-button>
        </vs-button-group>
      </div>

      <h4 v-else class="not-margin">
        {{'Registrar'}} <b>Incidente</b>
      </h4>

    </template>

    <div v-if=" activeButton === 0" class="space-y-6 ">
      <vs-input  primary class="block" type="number" v-model="dni_applicant" label="DNI postulante" >
        <template #message-success v-if="!is_dni" >
          DNI inválido (el DNI debe tener 8 dígitos)
        </template>
      </vs-input>

      <div class="focus-within:text-indigo-500 transition-colors duration-300 ease-in-out">
        <p class="textObservation">Observaciones</p>
        <textarea v-model="observation" class="custom-scrollbar bg-gray-100 w-full max-w-lg h-36 p-2  rounded-md text-gray-700 resize-none  overflow-y-scroll focus:outline-none focus:ring-0 border-b-2 focus:border-indigo-500"/>
      </div>

      <vs-button block :loading="isLoading" @click="save_incident"> {{id ? 'Actualizar' : 'Guardar'}} </vs-button>
    </div>

    <div v-else class="-mt-4 space-y-4">
      <span class="text-red-500">
        ¿Estás seguro de que deseas eliminar esta incidencia?
      </span> <br>
      <span>
        <b>Nota:</b> Esta acción no se puede deshacer una vez realizada.
      </span>
      <vs-button danger block @click="confirmDeletion">Eliminar</vs-button>
    </div>

  </vs-dialog>

  <vs-dialog v-if="confirm_deletion" v-model="confirm_deletion">
    <template #header>
      <h4 class="not-margin">
        Eliminar <b>incidente</b>
      </h4>
    </template>
    <div>¿Confirma la eliminación del incidente?</div>
    <template #footer>
     <div class="flex justify-end">
       <vs-button @click="confirm_deletion=false" transparent >Cancelar</vs-button>
       <vs-button :loading="isLoading" @click="deleteIncident">Aceptar</vs-button>
     </div>
    </template>
  </vs-dialog>

</div>
</template>

<style scoped >

.custom-scrollbar::-webkit-scrollbar {
  width: 7px;
  border-radius: 50px;
}
.custom-scrollbar::-webkit-scrollbar-track {
  background: rgb(243 244 246);
  border-radius: 50px;
}
.custom-scrollbar::-webkit-scrollbar-thumb {
  background-color: #4a90e2;
  border: 2px solid #f1f1f1;
}
.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background-color: #357ABD;
}
.textObservation{
  font-size: 12px;
  margin-left: 8px;
  margin-bottom: 2px;
}

</style>