<script>
import Form from "./components/Form.vue";
import ListIncidents from "./components/ListIncidents.vue";
// import SearchDni from "./components/SearchDni.vue";

export default {
  components: {Form, ListIncidents},
  data() {
    return {
      isRegister: false,
      incident: null,
    };
  },

  computed: {
    get_selected_incident(){
      return  this.$store.getters["incidente/get_selected_incident"]
    }
  },
  methods: {},

  created() {
    this.$store.dispatch("incidente/get_incident_list")
  },

  mounted() {},


  watch: {
    get_selected_incident(newVal) {
      if (newVal) {
        this.incident = newVal;
        this.isRegister = true;
        return
      }
      this.incident = null;
      this.isRegister = false;
    },
  },
};
</script>

<template>
  <div class="h-full space-y-2">

    <div class="rounded-2xl font-semibold text-2xl bg-gray-100 p-4">
      <span>Registro de Incidentes</span>
    </div>

    <vs-row justify="space-between" >
      <vs-button class="focus:outline-none" @click="()=> isRegister = true ">Resgistrar</vs-button>
<!--      <SearchDni />-->
    </vs-row>

    <Form
        v-if="isRegister"
        :item_incident="incident"
        @closeRegistration="isRegister = false"
    />

    <list-incidents />

    <pre>{{get_selected_incident}}</pre>

  </div>
</template>


