<script>
import { useUtils } from "@/utils/useUtils";
import SearchDni from "@/views/registro_incidente/components/SearchDni.vue";


export default {
  components: {SearchDni},
  data(){
    return{}
  } ,

  computed: {
    get_incidents(){
      return this.$store.getters["incidente/get_incident"]
    },
  },

  methods: {
    formatDate(date) {
      const { convertDate } = useUtils();
      return convertDate(date);
    },

    editIncident(item) {
      this.$store.commit("incidente/set_selected_incident", item)
    },

  }

}
</script>

<template>
  <div>
      <vs-table striped >
        <template #header  >
          <search-dni/>
        </template>

        <template #thead  >
          <vs-tr >
            <vs-th style="width:100px" >N° DNI</vs-th>
            <vs-th >Observación</vs-th>
            <vs-th >Fecha</vs-th>
          </vs-tr>
        </template>

        <template #tbody >
          <vs-tr v-for="(item, index) in get_incidents" :key="index"
                 :data="item"
                  @click="editIncident(item)"
          >
            <vs-td edit  >{{item.dni_applicant}}</vs-td>
            <vs-td >{{item.observation}}</vs-td>
            <vs-td >{{formatDate(item.date)}}</vs-td>
          </vs-tr>
        </template>

        <template #footer>
          <div style="user-select: none" class=" flex items-center justify-center bg-gray-100 p-2 "/>
        </template>

        <template #notFound>
          <span class="text-gray-400">No hay datos disponibles.😢</span>
        </template>
      </vs-table>

  </div>
</template>
